import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './MessageDetail.css';

const MessageDetail = ({ message, onClose, fetchMessages, setContent }) => {
  const [messageDetail, setMessageDetail] = useState(null); //   
  const [replyMode, setReplyMode] = useState(false); //   
  const [replyContent, setReplyContent] = useState(''); //   

  //  
  useEffect(() => {
    if (message && message.mnum) {
      axios.get(`/api/messages/detail/${message.mnum}`) // mnum    
        .then(response => setMessageDetail(response.data))
        .catch(error => console.error("Error fetching message:", error));
    }
  }, [message]);

  if (!messageDetail) {
    return <div>Loading...</div>; //   
  }

  const handleReplyMode = () => {
    setReplyMode(true); //   
  };

  const handleReply = () => {
    //   
    axios.post('/api/messages/send', {
      memId: messageDetail.friendId, //   ID
      friendId: messageDetail.memId, //  ID
      mcontent: replyContent,
    })
    .then(() => {
      alert(' .');
      setReplyContent(''); //   
      setReplyMode(false); //   
      onClose(); //  
      if (fetchMessages) fetchMessages(); //   
    })
    .catch(error => console.error('Error sending reply:', error));
  };

  const handleDelete = () => {
    axios.delete(`/api/messages/delete/${message.mnum}`)
      .then(() => {
        alert(' .');
        if (fetchMessages) fetchMessages(); //   
        setContent('inbox');  //    
      })
      .catch(error => console.error("Error deleting message:", error));
  };

  return (
    <div className="message-detail-container">

      <h2 className="message-detail-header">{messageDetail.memId}&nbsp;  !</h2>
      <p className="message-detail-content">{messageDetail.mcontent}</p>
      <p className="message-detail-date">
        {new Date(messageDetail.createSysdate).toLocaleString()}
      </p>

      {/*  textarea   */}
      <div className="message-actions">
        <button className="delete-button" onClick={() => setContent('inbox')}></button>
        <button className="delete-button" onClick={handleDelete}></button>
      </div>

        {!replyMode ? (
          <button className="reply-button" onClick={() => setReplyMode(true)}> </button>
        ) : (
          <div>
            <textarea
              className="reply-textarea"
              placeholder="  ..."
              value={replyContent}
              onChange={e => setReplyContent(e.target.value)}
            ></textarea>
            <button className="reply-button" onClick={handleReply}> </button>
          </div>
          
        )}
        
        
      </div>
    
  );
};

export default MessageDetail;
