import React, { useState } from 'react';
import axios from 'axios';
import './FriendsRequest.css';

function FriendsRequest({ memId = localStorage.getItem('id') }) { // loggedInUser    
  const [friendId, setFriendId] = useState(''); //  ID   
  const [message, setMessage] = useState(''); //      

  const sendFriendRequest = () => {
    //       
    if (friendId === memId) {
      setMessage('     .');
      return;
    }

    //  ID   
    axios.get('/member/exists', { params: { memId: friendId } })
      .then(response => {
        if (!response.data) {
          setMessage('   ID.');
          return;
        }

        //   
        console.log("  : memId = ", memId, ", friendId = ", friendId);

        axios.post('/friends/sendRequest', null, {
          params: {
            memId: memId, //    ID
            friendId: friendId, //   ID
          },
        })
        .then(response => {
          setMessage(response.data);
        })
        .catch(error => {
          console.error("  : ", error);
          setMessage('  .');
        });
      })
      .catch(error => {
        console.error("    :", error);
        setMessage('    .');
      });
  };

  return (
    <div className="friends-section-request">
      <h3> </h3>
      <input
        type="text"
        placeholder="ID "
        value={friendId} //    
        onChange={e => setFriendId(e.target.value)} //   
      />
      <div className="button-con">
        <button onClick={sendFriendRequest}> </button>
      </div>
      {message && <p>{message}</p>}
    </div>
  );
}

export default FriendsRequest;
