import React, { useState } from 'react';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';
import './InputDiary.css';

function InputDiary() {
    const { date } = useParams(); // URL  
    const navigate = useNavigate();
    const [dTitle, setDTitle] = useState('');
    const [dContent, setDContent] = useState('');

    const handleSubmit = (event) => {
        event.preventDefault();

        const userId = localStorage.getItem('id');
        const startDate = new Date(date).toISOString(); //  ISO  
        console.log(' dTitle : ', dTitle);
        console.log(' dContent : ', dContent);
        const diaryEntry = {
            dTitle : dTitle,
            dContent : dContent,
            memId : userId, //     ID
            start : startDate,
            end: startDate //    
        };
        console.log(' diaryEntry : ', diaryEntry)
        axios.post('/api/events/add', diaryEntry, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(() => {
            alert(' .');
            navigate(`/diary/${localStorage.getItem('id')}`); //    
        })
        .catch(error => {
            console.error('Error adding diary entry:', error);
            console.log(' diaryEntry : ', diaryEntry)
            alert('  .');
        });
    };

    return (
        <div className="note">
            <h2 className="inputdiary-title"> </h2>
            <form onSubmit={handleSubmit}>
                <div className="form-group">
                    {/* <label htmlFor="title"></label> */}
                    <input className="input-title"
                        placeholder=" ."
                        // type="text"
                        id="title"
                        value={dTitle}
                        onChange={(e) => setDTitle(e.target.value)}
                        required
                    />
                </div>
                <div className="form-content">
                    {/* <label htmlFor="content"></label> */}
                    <textarea className="input-content"
                        placeholder="  ."
                        id="content"
                        value={dContent}
                        onChange={(e) => setDContent(e.target.value)}
                        required
                    />
                </div>
                <button type="submit" className="diarysubmit"></button>
            </form>
        </div>
    );
}

export default InputDiary;
