import React, { useState, useEffect } from 'react';
import axios from 'axios';
import "./ProfileSection.css";
import defaultProfileImage from '../../img/basicProfile.png'; 
function ProfileSection({ hostId }) {
  const [profileImage, setProfileImage] = useState(''); //    
  const [selectedFile, setSelectedFile] = useState(null); //  
  const [comment, setComment] = useState(''); //  
  const [memId, setMemId] = useState(hostId); //  ID 
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isEditing, setIsEditing] = useState(false); //   

  // const defaultProfileImage = '../../img/basicProfile.png'; //   
  const [nick, setNick] = useState(localStorage.getItem('nickName')); //   

  //     
  useEffect(() => {
    if (!memId) {
      setError(' .');
      setLoading(false);
      return;
    }

    setMemId(hostId)
    console.log(' :',hostId)
    axios.get(`/member/get/${hostId}`)
      .then(response => {
        setProfileImage(response.data.imgPath || defaultProfileImage); //     
        setComment(response.data.comments || ''); //    
        setNick(response.data.nickname || '');
        setLoading(false);
      })
      .catch(() => {
        setError('     .');
        setLoading(false);
      });
  }, [hostId]);

  //     
  const handleFileChange = e => {
    const file = e.target.files[0];
    setSelectedFile(file);
    setProfileImage(URL.createObjectURL(file)); //  
  };

  //     
  const handleUpload = async () => {
    if (!selectedFile && !comment) {
      alert('  .');
      return;
    }

    const formData = new FormData();
    if (selectedFile) {
      formData.append('profileImage', selectedFile);
    }
    formData.append('memId', memId);
    formData.append('comment', comment); //  

    try {
      await axios.post('/member/upload', formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });
      alert('     .');
      setIsEditing(false); //     
      window.location.reload();
    } catch (error) {
      alert('       .');
    }
  };

  //    
  const handleDeleteProfileImage = async () => {
    try {
      await axios.delete('/member/deleteImage', { params: { memId: memId } }); //    
      setProfileImage(defaultProfileImage); //   
      setSelectedFile(null); //   
      alert('  .');
    } catch (error) {
      alert('     .');
    }
  };

  //    
  const toggleEditMode = () => {
    setIsEditing(!isEditing);
  };

  if (loading) return <div>Loading...</div>;
  if (error) return <div>{error}</div>;

  return (
    <div>
      <h2 className="profile">Profile</h2>
      <div className="profile-section">
        <div className="profile-image-container">
          <div className="basic-profile">
            <img
              id="basic-profile"
              src={profileImage || defaultProfileImage}
              alt=" "
              className={`profile-image ${isEditing ? 'editing' : ''}`} //     
            />
          </div>
        </div>

        {/*          */}
        {isEditing && (
          <>
            <label className="file-box" htmlFor="input-file">  </label>
            <input
              type="file"
              accept="image/*"
              onChange={handleFileChange}
              id="input-file"
            />
            <div className="ImgDbutton-container">
              <button onClick={handleDeleteProfileImage} className="profile-delete-btn"></button>
              <div className="ImgDtooltip"> </div>
            </div>
          </>
        )}
        <div className="label-container" cellspacing="0">
          <label className="nickname-label">{nick}</label>
          <label className="memId-label">({memId})</label>
        </div>
        <textarea
          placeholder=" ."
          value={comment}
          onChange={(e) => setComment(e.target.value)}
          rows="4"
          disabled={!isEditing}
          className={`textareabox ${isEditing ? 'editing' : ''}`} //     
        />

        {/*   hostId       */}
        {localStorage.getItem("id") === hostId && (
          isEditing ? (
            <button onClick={handleUpload} className="profile-save-btn"></button>
          ) : (
            <button onClick={toggleEditMode} className="profile-edit-btn"> </button>
          )
        )}
      </div>
    </div>
  );
}

export default ProfileSection;
