import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import './FindIdPassword.css'; //   

function FindIdPassword() {
  const [formData, setFormData] = useState({
    name: '',
    birthDate: '',
    phoneNumber: '',
    userId: '', //   
  });
  const [foundId, setFoundId] = useState(''); //   
  const [errorMessage, setErrorMessage] = useState(''); //   
  const [mode, setMode] = useState('findId'); //  'findId'  'findPassword' 
  const navigate = useNavigate();

  //    
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  //   
  const maskId = (id) => {
    if (id.length <= 4) {
      return `${id[0]}***`; //         
    }
    const visibleStart = id.slice(0, 2); //  2
    const visibleEnd = id.slice(-2); //  2
    return `${visibleStart}***${visibleEnd}`; //  *** 
  };

  //   
  const handleFindId = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.get('/member/findId', {
        params: {
          name: formData.name,
          birthday: formData.birthDate,
          phone: formData.phoneNumber,
        },
      });
      if (response.data) {
        const maskedId = maskId(response.data.memId); //  
        setFoundId(` ${maskedId}.`);
        setErrorMessage('');
      } else {
        setFoundId('');
        setErrorMessage('     .');
      }
    } catch (error) {
      setErrorMessage('     .');
      setFoundId('');
    }
  };

  //   
  const handleFindPassword = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.get('/member/findPassword', {
        params: {
          userId: formData.userId,
          name : formData.name,
          birthday: formData.birthDate,
          phone: formData.phoneNumber,
        },
      });
      if (response.data) {
        navigate(`/resetPassword?userId=${formData.userId}`); //    
      } else {
        setErrorMessage('     .');
      }
    } catch (error) {
      setErrorMessage('    .');
    }
  };

  return (
    <div className="find-container">
      <h2>{mode === 'findId' ? ' ' : ' '}</h2>
      <form onSubmit={mode === 'findId' ? handleFindId : handleFindPassword}>
        {mode === 'findPassword' && (
          <input
            type="text"
            name="userId"
            placeholder=""
            value={formData.userId}
            onChange={handleChange}
            required
          />
        )}
        <input
          type="text"
          name="name"
          placeholder=""
          value={formData.name}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="birthDate"
          placeholder=" (YYYYMMDD)"
          value={formData.birthDate}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="phoneNumber"
          placeholder=" "
          value={formData.phoneNumber}
          onChange={handleChange}
          required
        />
        <button className="find-submit" type="submit">
          {mode === 'findId' ? ' ' : ' '}
        </button>
      </form>
      {foundId && mode === 'findId' && <p style={{ color: 'green' }}>{foundId}</p>}
      {errorMessage && <p style={{ color: 'red' }}>{errorMessage}</p>}
      <div className="switch-mode">
        {mode === 'findId' ? (
          <p>
             ?{' '}
            <button onClick={() => setMode('findPassword')} className="switch-button">
               
            </button>
          </p>
        ) : (
          <p>
             ?{' '}
            <button onClick={() => setMode('findId')} className="switch-button">
               
            </button>
          </p>
        )}
      </div>

      {/*     */}
      <button className="go-home-button" onClick={() => {navigate('/')}}>
         
      </button>
    </div>
  );
}

export default FindIdPassword;
