import React, { useEffect, useRef, useState } from 'react';
import MusicList from './MusicList';  // MusicList 
import './MusicPlayerHandle.css';
import Draggable from 'react-draggable'; 
//npm install react-draggable 
//  

function MusicPlayerHandle({ currentSong, isPlaying, onPlayPauseClick, onNextPrevClick, allMusic, currentSongIndex, onSongSelect }) {
  const audioRef = useRef(null);  // audio    
  const [isRepeating, setIsRepeating] = useState(false);  //    
  const [showList, setShowList] = useState(false);  //     

  useEffect(() => {
    if (audioRef.current) {
      const audioElement = audioRef.current;

      audioElement.addEventListener('ended', () => {
        onNextPrevClick(true);  // true     
      });

      if (isPlaying) {
        audioElement.play().catch(error => console.error("Audio play error:", error));  //    
      } else {
        audioElement.pause();  // 
      }
    }
  }, [isPlaying, currentSong, onNextPrevClick]);

  const handleRepeatToggle = () => {
    setIsRepeating(prevState => !prevState);  //   
  };

  const toggleList = () => {
    setShowList(prevState => !prevState);  // showList      
  };

  return (
    <div className="music-player-wrapper">
      {/*    music__inner  */}
      <Draggable>
        <div className="music__inner">
          {/*      */}
          {/* <div className="music__top">
            <h3>Now Playing</h3>
          </div> */}

          {/*      */}
          <div className="music__song">
            <p>{currentSong.name} - {currentSong.artist}</p>
          </div>

          {/*   */}
          <audio
            src={`/songs/${currentSong.audio}.mp3`}  //      
            ref={audioRef}  // audioRef    
            autoPlay={isPlaying}  // isPlaying    
            controls  //    
            loop={isRepeating}  // isRepeating     
            onError={(e) => console.error("Failed to load audio:", e)}
          >
            Your browser does not support the audio element.  {/*       */}
          </audio>

          {/*     */}
          <div className="music__progress">
            <button onClick={() => onNextPrevClick(false)} className="before-music"/>  {/*   */}
            <button onClick={onPlayPauseClick} className={isPlaying ? "stop-music" : "play-music"} />  {/* / */}
            <button onClick={() => onNextPrevClick(true)} className="next-music" />  {/*   */}
            <button onClick={handleRepeatToggle}
              className={isRepeating ? "repeatOne-music" : "repeat-music"} /> {/*      */}
            <button onClick={toggleList} className="list-music"/>  {/*    */}
          </div>
        </div>
      </Draggable>

      {/*     */}
      <div className={`music__list-panel ${showList ? 'show' : ''}`}>
        <MusicList
          allMusic={allMusic}  //    MusicList  
          onSongSelect={onSongSelect}  //      
          currentSongIndex={currentSongIndex}  //      
        />
      </div>
    </div>
  );
}

export default MusicPlayerHandle;
