import { useState } from "react";
import { useNavigate } from "react-router-dom";
import './BoardUpload.css';

function BoardUpload({ hostId, setHostId }) {
  const [selectedImages, setSelectedImages] = useState([]);
  const [bTitle, setBTitle] = useState('');
  const [bContent, setBContent] = useState('');
  const navigate = useNavigate();

  //    
  const handleImageChange = (event) => {
    const files = Array.from(event.target.files); //    
    const newImages = files.map(file => ({
      file,
      preview: URL.createObjectURL(file)  //   URL 
    }));
    setSelectedImages(prevImages => [...prevImages, ...newImages]); //   
  };

  //   
  const handleRemoveImage = (index) => {
    setSelectedImages(prevImages => 
      prevImages.filter((_, i) => i !== index)  //      
    );
  };

  //   
  const handleSubmit = async (event) => {
    event.preventDefault(); //    

    const formData = new FormData();
    formData.append("bTitle", bTitle);
    formData.append("bContent", bContent);
    formData.append("memId", hostId);
    
    //   FormData 
    selectedImages.forEach((image, index) => {
      formData.append(`image`, image.file);
    });

    try {
      const response = await fetch("/board/write", {
        method: "POST",
        body: formData,
      });

      if (response.ok) {
        alert("  ");
        setSelectedImages([]);  //   
        setBTitle('');
        setBContent('')
        navigate(`/bulletin-board/${hostId}`);  //  
      } else {
        alert("  ");
      }
    } catch (error) {
      console.error("  :", error);
      alert("    .");
    }
  };
  const BoardList = () => {
    navigate(`/bulletin-board/${ hostId }`);  //   
  };

  const handleGoBack = () => {
    navigate(-1); //   
  };

  return (
    <form onSubmit={handleSubmit}>
      <div className="BoardUpload-box">
        <h6 className="BUTitle"> </h6>
        <div className="B-ImgPrevBox">
          <div className="BoardUpload-Imgbox">
            {selectedImages.length > 0 && (
              <div style={{ display: "flex", flexDirection: "row", gap: "10px" }}>
                {selectedImages.map((image, index) => (
                  <div key={index}>
                    <img
                      className="board-Imgs"
                      src={image.preview}
                      alt={`Selected ${index}`}
                      style={{ width: "150px", height: "150px", objectFit: "cover" }}
                    />
                    <button
                      className="board-ImgD"
                      type="button"
                      onClick={() => handleRemoveImage(index)}
                      style={{ display: "flex", flexDirection: "row", gap: "10px" }}
                    />
                  </div>
                ))}
              </div>
            )}
          </div>
          <label className="B-ImgBox">
            <input
              className="board-uploadImgBox"
              type="file" 
              accept="image/*" 
              multiple  //    
              onChange={handleImageChange} 
            />+
          </label>
        </div>
        <div className="BoardContentBox">
          <div>
            <input
              className="BoardUploadTitle"
              placeholder=" ."
              type="text" 
              value={bTitle} 
              onChange={(e) => setBTitle(e.target.value)} 
            />
          </div>
          <div>
            <textarea
              className="BoardUploadContent"
              placeholder="  ."
              value={bContent} 
              onChange={(e) => setBContent(e.target.value)} 
            />
          </div>
          <button type="button" className="BUC" onClick={handleGoBack}>
            
          </button>
          <button type="submit" className="BUB">
            
          </button>
        </div>
        </div>
    </form>
  );
}

export default BoardUpload;
