import axios from 'axios';
import React, { useState, useEffect} from 'react';
import './WalkingCourse.css';
import { useNavigate, Navigate  } from 'react-router-dom';

function WalkingCourse() {
    const [selectedRegion, setSelectedRegion] = useState('');
    const [subRegions, setSubRegions] = useState([]);
    const [filteredSubRegions, setFilteredSubRegions] = useState([]);
    const [walkingCourses, setWalkingCourses] = useState([]);
    const [selectedCourseDetails, setSelectedCourseDetails] = useState(null);
    const [regions] = useState([
        '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', ''
    ]);
    //  
    const [dropdownVisible, setDropdownVisible] = useState(false);
    const navigate = useNavigate();
    const handleRegionChange = (e) => {
        const region = e.target.value;
        setSelectedRegion(region);

        if (region) {
            axios.get(`/api/walking/subregions/${region}`)
                .then(response => {
                    setSubRegions(response.data);
                    setFilteredSubRegions(response.data);
                })
                .catch(error => {
                    console.error('Error:', error);
                });
        } else {
            setSubRegions([]);
            setFilteredSubRegions([]);
        }
    };

    const handleSubRegionChange = (e) => {
        const subRegion = e.target.value;
        fetchWalkingCourse(subRegion);
    };

    const fetchWalkingCourse = (subRegion) => {
        axios.get('/api/walking/courses', {
            params: { subRegion: subRegion }
        })
            .then(response => {
                setWalkingCourses(response.data);
            })
            .catch(error => {
                console.error('Error:', error);
            });
    };

    const handleCourseClick = (courseId) => {
        axios.get(`/api/walking/courses/${courseId}`)
            .then(response => {
                setSelectedCourseDetails(response.data);
            })
            .catch(error => {
                console.error('Error fetching course details:', error);
            });
    };

    const handleViewVotes = () => {
        //     
        window.location.href = '/votes'; //  navigate('/votes')   
    };

    const handleCreateVote = () => {
        //     
        window.location.href = '/create-vote'; //  navigate('/create-vote')   
    };

    useEffect(() => {
        if (selectedRegion) {
            setFilteredSubRegions(subRegions.filter(subRegion => subRegion !== selectedRegion));
        } else {
            setFilteredSubRegions(subRegions);
        }
    }, [selectedRegion, subRegions]);

    //  
    const toggleDropdown = () => {
        setDropdownVisible(!dropdownVisible);
    };

    const handleVoteCreate = () => {
        navigate(`/vote-create/${localStorage.getItem("id")}`); //   
    };

    const VoteList = () => {
        navigate(`/VoteList/${localStorage.getItem("id")}`);
    };

    return (
        <div className='walking-Box'>
            <div className='walking-search'>
                <h1> </h1>
                
                <div>
                    <label htmlFor="region-select"> :</label>
                    <select
                        id="region-select"
                        value={selectedRegion}
                        onChange={handleRegionChange}
                    >
                        <option value="">--   --</option>
                        {regions.map(region => (
                            <option key={region} value={region}>{region}</option>
                        ))}
                    </select>
                </div>
                
                {selectedRegion && (
                    <div>
                        <label htmlFor="subregion-select">  :</label>
                        <select
                            id="subregion-select"
                            onChange={handleSubRegionChange}
                        >
                            <option value="">--    --</option>
                            {filteredSubRegions.map(subRegion => (
                                <option key={subRegion} value={subRegion}>{subRegion.slice(3)}</option>
                            ))}
                        </select>
                    </div>
                )}
                <div>
                    <h2> </h2>
                    <ul>
                        {walkingCourses.length > 0 ? (
                            walkingCourses.map((course, index) => (
                                <li key={index} onClick={() => handleCourseClick(course.esntlId)}>
                                    {course.walkCourseName}
                                </li>
                            ))
                        ) : (
                            <li>  .</li>
                        )}
                    </ul>
                </div>
                
            </div>
            
            <div className='detailWalking'>
                <h2></h2>
                {selectedCourseDetails ? (
                    <table>
                        <tbody>
                            <tr>
                                <td className='walkCourseName' colSpan={7} style={{textAlign:'center'}}>{selectedCourseDetails.walkCourseName}</td>
                            </tr>
                            <tr>
                                <td></td><td className='courseLevelName'>{selectedCourseDetails.courseLevelName}</td>
                                <td className='courseLengthContent'></td><td >{selectedCourseDetails.courseLengthContent}</td>
                                <td  className='courseTimeContent'> </td><td>{selectedCourseDetails.courseTimeContent}</td>
                            </tr>
                            <tr>
                                <td></td><td className='address' colSpan={7}>{selectedCourseDetails.address}</td>
                            </tr>
                            <tr>
                                <td> </td><td className='courseDescription' colSpan={7}>{selectedCourseDetails.courseDescription.replace(/(\?|\-|\~)/g, match => {
                                    if (match === '?') return '';
                                    if (match === '-') return '';
                                    if (match === '~') return '';
                                    return match; //      
                                    })}</td>
                            </tr>
                            <tr>
                                <td></td><td className='additionalDescription' colSpan={7}>{selectedCourseDetails.additionalDescription}</td>
                            </tr>
                            <tr>
                                <td></td><td className='optionDescription' colSpan={7}>{selectedCourseDetails.optionDescription}</td>
                            </tr>
                        </tbody>
                    </table>
                ) : (
                    <p>   .</p>
                )}
            </div>

            {/*     */}
            <div className="vote">
                <button className="vote-button" onClick={toggleDropdown}></button>
                <div className={`dropdown-menu ${dropdownVisible ? 'show' : ''}`}>
                    <button onClick={handleVoteCreate}> </button>
                    <button onClick={VoteList}> </button>
                </div>
            </div>
            
        </div>
    );
}

export default WalkingCourse;
