import React, { useState, useEffect } from 'react';
import Modal from '../message/Modal'; //   
import './CommentsSection.css'; //   .
import axios from 'axios';

function CommentsSection() {
  const [isModalOpen, setIsModalOpen] = useState(false); //   
  const [modalContent, setModalContent] = useState('inbox'); //     
  const [selectedMessage, setSelectedMessage] = useState(null); //    
  const [hasUnreadMessages, setHasUnreadMessages] = useState(false); //      

  //     
  const checkUnreadMessages = () => {
    axios.get(`/api/messages/received/${localStorage.getItem("id")}`)
      .then(response => {
        const unreadMessages = response.data.some(message => message.isReading === 1);
        setHasUnreadMessages(unreadMessages);  //      
      })
      .catch(error => console.error("Error fetching messages:", error));
  };

  //       
  useEffect(() => {
    checkUnreadMessages();  //        
  }, []);

  const openModal = (content) => {
    setModalContent(content); //    (inbox, writeMessage )
    setIsModalOpen(true); //  
  };

  const closeModal = () => {
    setIsModalOpen(false);
    checkUnreadMessages();  //        
  };

  return (
    <div>
      <div className="comments-actions">
        {/*    */}
        <button className="letter-button" onClick={() => openModal('writeMessage')}>
           
        </button>

        {/*   */}
        <button className="letter-button" onClick={() => openModal('inbox')}>
          
        </button>

        {/* letterIcon    */}
        <div 
          className={`letterIcon ${hasUnreadMessages ? 'unread' : 'read'}`} 
          // onClick={() => openModal('inbox')}
          >
        </div>
      </div>

      {/*        */}
      <Modal
        isOpen={isModalOpen}
        onClose={closeModal}
        content={modalContent}
        setContent={setModalContent}
        selectedMessage={selectedMessage} //   
        setSelectedMessage={setSelectedMessage} //     
        checkUnreadMessages={checkUnreadMessages} //     
      />
    </div>
  );
}

export default CommentsSection;
