import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './TopComments.css';

function TopComments() {
  const [isEditing, setIsEditing] = useState(false); //   
  const [greeting, setGreeting] = useState(' '); //  
  const [newGreeting, setNewGreeting] = useState(''); //    
  const [selectedFile, setSelectedFile] = useState(null); //   (  )
  const memId = localStorage.getItem('id'); //  ID  ( )

  
  //    
  useEffect(() => {
    if (!memId) {
      return;
    }

    axios.get(`/member/get/${memId}`)
      .then(response => {
        setGreeting(response.data.greeting || ''); //    
        setNewGreeting(response.data.greeting || ''); //     
      })
      .catch(() => {
        console.error('     .');
      });
  }, [memId]);

  //   
  const toggleEdit = () => {
    setIsEditing(!isEditing);
  };

  //     
  const handleInputChange = (e) => {
    setNewGreeting(e.target.value);
  };

  //     
  const handleFileChange = (e) => {
    setSelectedFile(e.target.files[0]); //    
  };

  //     ( )
  const saveGreeting = async () => {
    const formData = new FormData(); // FormData  
    formData.append('memId', memId); //  ID 
    formData.append('greeting', newGreeting); //   

    //    FormData  
    if (selectedFile) {
      formData.append('profileImage', selectedFile);
    }

    try {
      //    
      await axios.post('/member/greeting', formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });

      //    
      setGreeting(newGreeting);
      setIsEditing(false); //   
      alert('  .');
    } catch (error) {
      console.error('Error saving greeting:', error);
      alert('    .');
    }
  };
    //    
    const toggleEditMode = () => {
      setIsEditing(!isEditing);
    };
  
  return (
    <div>
      {!isEditing ? (
        <div>
          <h4 className="homgepage-Co">{greeting || '  '}</h4>
          <button className="homgepage-Co-edit" onClick={toggleEdit}></button>
        </div>
      ) : (
        <div>
          <input 
            type="text" 
            value={newGreeting} 
            onChange={handleInputChange}
            className={`Top-co-input ${isEditing ? 'editing' : ''}`} //      
          />
          <button className="homgepage-Co-edit" onClick={saveGreeting}></button>
        </div>
      )}
    </div>
  );
}

export default TopComments;
