import React, { useEffect, useState } from 'react';
import axios from 'axios'; // axios 
import './ProfileEdit.css'; //  
import { useNavigate } from 'react-router-dom';

function ProfileEdit() {
  //       
  const [form, setForm] = useState({ 
    nickname: '',
    email: '',
    name: '',
    password: '',
    confirmPassword: '',
    phone: '',
    address: '',
    birthday: '',
    gender: '',
    imgName: '',
    imgPath: '',
    comments: '',
    greeting: '',
  });
  const [errorMessage, setErrorMessage] = useState(''); //   
  const [successMessage, setSuccessMessage] = useState(''); //    
  const [userId, setUserId] = useState(localStorage.getItem('id'))  //    

  const navigate = useNavigate();
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await axios.get(`/member/get/${userId}`);
        
        //      
        const { nickname, email, name, phone, address, birthday, gender, imgName, imgPath, comments, greeting } = response.data;

        setForm({
          nickname: nickname || '',
          email: email || '',
          name: name || '',
          password: '', //    
          confirmPassword: '', //   
          phone: phone || '',
          address: address || '',
          birthday: birthday || '',
          gender: gender || '',
          imgName: imgName || '',
          imgPath: imgPath || '',
          comments: comments || '',
          greeting: greeting || '',
        });

      } catch (error) {
        setErrorMessage('     .');
        console.error('Error fetching user data:', error);
      }
    };

    fetchUserData();
  }, [userId]);

 

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prevForm) => ({
      ...prevForm,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    //    
    if (!form.nickname || !form.name || !form.password) {
      setErrorMessage(', ,    .');
      return;
    }

    //   
    if (form.password !== form.confirmPassword) {
      setErrorMessage('  .');
      return;
    }

    axios.post('/member/updateProfile', {
      memId: userId,  //    ID
      nickname: form.nickname,
      email: form.email,
      name: form.name,
      pass: form.password,
      phone: form.phone,
      address: form.address,
      birthday: form.birthday,
      gender: form.gender,
      imgName: form.imgName,
      imgPath: form.imgPath,
      comments: form.comments,
      greeting: form.greeting,
    })
    .then((response) => {
      if (response.data === 'success') {
        setSuccessMessage('  .');
        setErrorMessage(''); //     
        navigate(`/home/${userId}`)
      } else {
        setErrorMessage('    .');
        setSuccessMessage(''); //     
      }
    })
    .catch((error) => {
      setErrorMessage('    .');
      setSuccessMessage('');
      console.error('Error:', error);
    });
  };

  return (
    <>
    <div className="profile-edit-container">
      <h2 className="profile-edit-title"> </h2>
      <form onSubmit={handleSubmit} className="profile-edit-form">
        <div className="profile-image-upload">
          <div className="image-placeholder">
            {/*       */}
          </div>
          <p className="image-note">*    .</p>
        </div>
        <div className="profile-edit-fields">
          <input
            type="text"
            name="nickname"
            placeholder="  ."
            value={form.nickname}
            onChange={handleChange}
            className="profile-input"
            required
          />
          <input
            type="email"
            name="email"
            placeholder="  ."
            value={form.email}
            onChange={handleChange}
            className="profile-input"
          />
          <input
            type="text"
            name="name"
            placeholder=" ."
            value={form.name}
            onChange={handleChange}
            className="profile-input"
            required
          />
          <input
            type="password"
            name="password"
            placeholder="  ."
            value={form.password}
            onChange={handleChange}
            className="profile-input"
            required
          />
          <input
            type="password"
            name="confirmPassword"
            placeholder="  ."
            value={form.confirmPassword}
            onChange={handleChange}
            className="profile-input"
            required
          />
          <input
            type="text"
            name="address"
            placeholder="  ."
            value={form.address}
            onChange={handleChange}
            className="profile-input"
          />
          <input
            type="text"
            name="birthday"
            placeholder=" ."
            value={form.birthday}
            onChange={handleChange}
            className="profile-input"
          />
          <input
            type="text"
            name="gender"
            placeholder=" ."
            value={form.gender}
            onChange={handleChange}
            className="profile-input"
          />
        </div>
        <div className="profile-edit-buttons">
          <button type="button" className="profile-button cancel" onClick={() => {
            navigate(-1);
          }}></button>
          <button type="submit" className="profile-button save"></button>
        </div>
      </form>
      </div>
      {errorMessage && <p className="error-message">{errorMessage}</p>}
      {successMessage && <p className="success-message">{successMessage}</p>}
    </>
  );
}

export default ProfileEdit;
