import React, { useEffect, useState } from "react";
import { Link } from 'react-router-dom'; // Link   
import "./RestaurantMap.css"; //   

function FavoriteList() {
  const [favorites, setFavorites] = useState([]); //  
  const [selectedRestaurant, setSelectedRestaurant] = useState(null); //  
  const [map, setMap] = useState(null); //   
  const [markers, setMarkers] = useState([]); //   

  //    
  const loadNaverMapScript = () => {
    return new Promise((resolve, reject) => {
      if (window.naver && window.naver.maps) {
        resolve();
      } else {
        const script = document.createElement("script");
        script.src = `https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=xjh0qjchu1`;
        script.async = true;

        script.onload = () => {
          if (window.naver && window.naver.maps) {
            resolve();
          } else {
            reject(new Error("Naver Map API failed to load"));
          }
        };

        script.onerror = () => {
          reject(new Error("Naver Map script load error"));
        };

        document.head.appendChild(script);
      }
    });
  };

  //    
  const fetchFavorites = () => {
    fetch(`/api/favorites/list?memId=${localStorage.getItem('id')}`)  
      .then((res) => res.json())
      .then((data) => {
        if (Array.isArray(data)) {
          setFavorites(data);
          console.log("Fetched data:", data); //    
          if (map) { // map    
            displayMarkersOnMap(data);
          }
        } else {
          console.error('Received data is not an array', data);
          setFavorites([]);  //  
        }
      })
      .catch((error) => {
        console.error('Error fetching favorites', error);
        setFavorites([]);  //       
      });
  };

  //    
  const deleteFavorite = (restaurantId) => {
    const memId = localStorage.getItem('id');  //   

    fetch(`/api/favorites/delete?memId=${memId}&restaurantId=${restaurantId}`, {
      method: "DELETE",
    })
      .then((res) => {
        if (res.ok) {
          alert("  .");
          //     
          fetchFavorites();
          removeMarker(restaurantId); //  
          setSelectedRestaurant(null);  //   
        } else {
          alert("   .");
        }
      })
      .catch((error) => {
        console.error("Error deleting favorite", error);
      });
  };

  //    (  )
  const displayMarkersOnMap = (restaurants) => {
    if (!map) return; // map     

    clearMarkers(); //   
    const newMarkers = [];
    restaurants.forEach((restaurant) => {
      const position = new window.naver.maps.LatLng(restaurant.restaurant.latitude, restaurant.restaurant.longitude);

      const marker = new window.naver.maps.Marker({
        position: position,
        map: map,
        title: restaurant.restaurant.rnum, //  rnum 
      });

      const infoWindowContent = `
        <div style="padding:10px; cursor:pointer;" id="infoWindow-${restaurant.restaurant.rnum}">
          <strong>${restaurant.restaurant.rname}</strong><br>${restaurant.restaurant.raddress || ' '}
        </div>`;

      const infoWindow = new window.naver.maps.InfoWindow({
        content: infoWindowContent,
      });

      window.naver.maps.Event.addListener(marker, "click", () => {
        infoWindow.open(map, marker);
      });

      newMarkers.push(marker);
    });
    setMarkers(newMarkers);
  };

  //    
  const removeMarker = (restaurantId) => {
    const updatedMarkers = markers.filter((marker) => {
      if (marker.title === restaurantId) {
        marker.setMap(null); //   
        return false;
      }
      return true;
    });
    setMarkers(updatedMarkers);
  };

  //    
  const clearMarkers = () => {
    markers.forEach((marker) => marker.setMap(null)); //    
    setMarkers([]); //   
  };

  //  
  const initializeMap = () => {
    const mapOptions = {
      center: new window.naver.maps.LatLng(37.494669, 127.030103),
      zoom: 15,
    };
    const naverMap = new window.naver.maps.Map("map", mapOptions);
    setMap(naverMap);
  };

  useEffect(() => {
    loadNaverMapScript()
      .then(() => {
        initializeMap();
      })
      .catch((error) => {
        console.error("Error loading Naver Map API:", error);
      });
  }, []);

  useEffect(() => {
    if (map) {
      fetchFavorites(); // map     
    }
  }, [map]); // map     

  //      
  const handleRestaurantClick = (restaurant) => {
    setSelectedRestaurant(restaurant);
  };

  return (
    <div className="container">
      <div id="map" className="map"></div>

      <div className="bar">
        {/*          */}
        <div className="search-container">
          <div className="search-bar">
            <Link to={`/restaurants/${localStorage.getItem("id")}`}>
              <button></button>
            </Link>
          </div>

          <div className="favorite-button">
            <Link to={`/favorites/${localStorage.getItem("id")}`}>
              <button> </button>
            </Link>
          </div>
        </div>

        <h3>  </h3>
        <div className="restaurant-Flist">
          {selectedRestaurant ? (
            <div className="restaurant-details">
              <h5>{selectedRestaurant.rname}</h5>
              <p>: {selectedRestaurant.raddress}</p>
              <p>: {selectedRestaurant.telNo || " "}</p>
              <button onClick={() => deleteFavorite(selectedRestaurant.rnum)}></button>
              <button onClick={() => setSelectedRestaurant(null)}></button>
            </div>
          ) : (
            <ul>
              {favorites.length === 0 ? (
                <p>  .</p>
              ) : (
                favorites.map((favorite) => (
                  <li key={favorite.restaurant.rnum} onClick={() => handleRestaurantClick(favorite.restaurant)}>
                    <strong>{favorite.restaurant.rname}</strong>
                    <p>: {favorite.restaurant.raddress}</p>
                    <p>: {favorite.restaurant.telNo}</p>
                  </li>
                ))
              )}
            </ul>
          )}
        </div>
      </div>
    </div>
  );
}

export default FavoriteList;