import React, { useEffect, useState } from "react";
import { Link } from 'react-router-dom'; // Link   
import "./RestaurantMap.css"; //   

function RestaurantMap() {
  const [restaurants, setRestaurants] = useState([]);
  const [selectedRestaurant, setSelectedRestaurant] = useState(null); //  
  const [map, setMap] = useState(null);
  const [markers, setMarkers] = useState([]); //   
  const [searchKeyword, setSearchKeyword] = useState(""); //    
  const [isAddingFavorite, setIsAddingFavorite] = useState(false); //    

  //      
  const loadNaverMapScript = () => {
    return new Promise((resolve, reject) => {
      if (window.naver && window.naver.maps) {
        resolve();
      } else {
        const script = document.createElement("script");
        script.src = `https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=xjh0qjchu1`; //   ID
        script.async = true;

        script.onload = () => {
          if (window.naver && window.naver.maps) {
            resolve();
          } else {
            reject(new Error("Naver Map API failed to load"));
          }
        };

        script.onerror = () => {
          reject(new Error("Naver Map script load error"));
        };

        document.head.appendChild(script);
      }
    });
  };

  //   
  const clearMarkers = () => {
    markers.forEach((marker) => marker.setMap(null)); //   
    setMarkers([]); //   
  };

  // SK  API    
  const fetchRestaurants = (keyword) => {
    setRestaurants([]);  //   
    clearMarkers();   //   

    const url = `https://apis.openapi.sk.com/tmap/pois?version=1&searchKeyword=${keyword}&page=1&searchType=all&count=20&resCoordType=WGS84GEO&multiPoint=N&searchtypCd=A&reqCoordType=WGS84GEO&poiGroupYn=N`;

    fetch(url, {
      headers: {
        Accept: "application/json",
        appKey: "PvNF6A578c9De272T5Qz0auxd92lssbk3nmXey3c", // SK API Key 
      },
    })
      .then((response) => response.json())
      .then((data) => {
        console.log("API  :", data);

        let restaurantData = data?.searchPoiInfo?.pois?.poi;
        if (Array.isArray(restaurantData)) {
          // ""      
          restaurantData = restaurantData.filter(restaurant => !restaurant.name.includes(""));
          setRestaurants(restaurantData);
          displayMarkersOnMap(restaurantData); //    
          moveMapToCenter(restaurantData); //       
        } else {
          console.error("Expected an array but got:", data);
        }
      })
      .catch((error) => {
        console.error("Error fetching restaurants:", error);
      });
  };

  //     
  const displayMarkersOnMap = (restaurants) => {
    if (map) {
      const newMarkers = [];
      restaurants.forEach((restaurant) => {
        const position = new window.naver.maps.LatLng(restaurant.frontLat, restaurant.frontLon);

        //   []    
        const restaurantName = restaurant.name.replace(/\[.*?\]/g, "").replace("", "").trim();
        const address = `${restaurant.upperAddrName || ''} ${restaurant.middleAddrName || ''} ${restaurant.roadName || ''} ${restaurant.firstBuildNo || ''}`.trim();

        const marker = new window.naver.maps.Marker({
          position: position,
          map: map,
          title: restaurantName,
        });

        const infoWindowContent = `
          <div style="padding:10px; cursor:pointer;" id="infoWindow-${restaurant.id}">
            <strong>${restaurantName}</strong><br>${address}
          </div>`;

        const infoWindow = new window.naver.maps.InfoWindow({
          content: infoWindowContent,
        });

        //    InfoWindow 
        window.naver.maps.Event.addListener(marker, "click", () => {
          infoWindow.open(map, marker);
        // InfoWindow       
        setTimeout(() => {
          const infoWindowElement = document.getElementById(`infoWindow-${restaurant.id}`);
            if (infoWindowElement) {
              infoWindowElement.addEventListener("click", () => {
                handleRestaurantClick({ ...restaurant, address, name: restaurantName }); // InfoWindow    
              });
            }
          }, 0); // 0ms  InfoWindow     
        });

        newMarkers.push(marker);
      });
      setMarkers(newMarkers); //   
    }
  };

  //       
  const moveMapToCenter = (restaurants) => {
    if (map && restaurants.length > 0) {
      const latitudes = restaurants.map((restaurant) => parseFloat(restaurant.frontLat));
      const longitudes = restaurants.map((restaurant) => parseFloat(restaurant.frontLon));

      const avgLatitude = latitudes.reduce((acc, lat) => acc + lat, 0) / latitudes.length;
      const avgLongitude = longitudes.reduce((acc, lon) => acc + lon, 0) / longitudes.length;

      const newCenter = new window.naver.maps.LatLng(avgLatitude, avgLongitude);
      map.setCenter(newCenter); //   
    }
  };

  //    
  const initializeMap = () => {
    const mapOptions = {
      center: new window.naver.maps.LatLng(37.494669, 127.030103),
      zoom: 15,
    };
    const naverMap = new window.naver.maps.Map("map", mapOptions);
    setMap(naverMap);
  };

  useEffect(() => {
    loadNaverMapScript()
      .then(() => {
        console.log("Naver Map API loaded successfully");
        initializeMap();
        fetchRestaurants(searchKeyword); // SK API   
      })
      .catch((error) => {
        console.error("Error loading Naver Map API:", error);
      });
  }, []);

  //      
  const handleSearch = () => {
    fetchRestaurants(searchKeyword + ""); //     
  };

  //      
  const handleRestaurantClick = (restaurant) => {
    const address = `${restaurant.upperAddrName || ''} ${restaurant.middleAddrName || ''} ${restaurant.roadName || ''} ${restaurant.firstBuildNo || ''}`.trim();
    const restaurantName = restaurant.name.replace(/\[.*?\]/g, "").trim(); // []    
  
    setSelectedRestaurant({
      id: restaurant.id, // id  
      name: restaurantName,
      address: address,
      detailBizName: restaurant.detailBizName || " ",
      frontLat: restaurant.frontLat || 0,
      frontLon: restaurant.frontLon || 0,
      telNo: restaurant.telNo? restaurant.telNo : " ", //     
      menu: restaurant.menu || "  ",
    });
  };


  //     
  const handleFavorite = (restaurant) => {
    if (!restaurant || !restaurant.id || !restaurant.name || !restaurant.frontLat || !restaurant.frontLon) {
      alert("    .");
      console.log("Invalid restaurant object:", restaurant);
      return;
    }

    setIsAddingFavorite(true); //    

    fetch(`/api/favorites/add?memId=${localStorage.getItem("id")}&restaurantId=${restaurant.id}&name=${restaurant.name}&address=${restaurant.address}&foodType=${restaurant.detailBizName}&latitude=${restaurant.frontLat}&longitude=${restaurant.frontLon}&telNo=${restaurant.telNo}`, {
      method: "POST",
    })
      .then((response) => {
        if (response.ok) {
          alert("  !");
        } else {
          return response.json().then((data) => {
            alert(data.message);  //     
          });
        }
      })
      .finally(() => {
        setIsAddingFavorite(false); //     
      })
      .catch((error) => console.error("Error adding favorite:", error));
  };

  //      
  const handleBackToList = () => {
    setSelectedRestaurant(null);
  };

  return (
    <div style={{ position: "relative", width: "100%", height: "600px" }}>
      {/*    */}
      <div id="map" className="map"></div>

      <div className="bar">
        {/*          */}
        <div className="search-container">
          <div className="search-bar">
            <Link to={`/restaurants/${localStorage.getItem("id")}`}>
              <button></button>
            </Link>
          </div>

          <div className="favorite-button">
            <Link to={`/favorites/${localStorage.getItem("id")}`}>
              <button> </button>
            </Link>
          </div>
        </div>

        <h3> </h3>
        <div className="search-input-group">
          <input
            type="text"
            value={searchKeyword}
            onChange={(e) => setSearchKeyword(e.target.value)}
            placeholder="   "
          />
          <button onClick={handleSearch}></button>
        </div>

        {/*        */}
        <div className="restaurant-list">
          {selectedRestaurant ? (
            <div className="restaurant-details">
              <h5>{selectedRestaurant.name}</h5>
              <p>: {selectedRestaurant.address}</p>
              <p>: {selectedRestaurant.telNo}</p>
              <p>: {selectedRestaurant.detailBizName}</p>
              <p>: {selectedRestaurant.menu}</p>
              <button onClick={() => handleFavorite(selectedRestaurant)} disabled={isAddingFavorite}>
                 
              </button>
              <button onClick={handleBackToList}></button>
            </div>
          ) : (
            <ul>
              {restaurants.length === 0 ? (
                <p>  .</p>
              ) : (
                restaurants
                  .map((restaurant) => {
                    const address = `${restaurant.upperAddrName || ''} ${restaurant.middleAddrName || ''} ${restaurant.roadName || ''} ${restaurant.firstBuildNo || ''}`.trim();
                    const restaurantName = restaurant.name.replace(/\[.*?\]/g, "").trim();
                    return (
                      <li key={restaurant.id} onClick={() => handleRestaurantClick(restaurant)}>
                        <strong>{restaurantName}</strong>
                        <p>: {address}</p>
                        <p>: {restaurant.telNo}</p>
                      </li>
                    );
                  })
              )}
            </ul>
          )}
        </div>
      </div>
    </div>
  );
}

export default RestaurantMap;