import React, { useState, useEffect } from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction'; //       
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import koLocale from '@fullcalendar/core/locales/ko'; //   
import './MyCalendar.css'; // CSS  .

// 
//npm install @fullcalendar/react @fullcalendar/daygrid @fullcalendar/interaction


function MyCalendar({hostId}) {
    const [events, setEvents] = useState([]); //     
    const navigate = useNavigate(); //    navigate 
    useEffect(() => {
        fetchEvents(); //       
    }, [hostId]);

    //     
    const fetchEvents = () => {
        axios.get(`/api/events/all/${hostId}`) //  API 
            .then(response => setEvents(response.data)) //     
            .catch(error => console.error('Error fetching events:', error)); //      
    };

    //     
    const handleDateClick = (arg) => {
        if(hostId === localStorage.getItem('id'))
        navigate(`/inputDiary/${arg.dateStr}/${hostId}`); //   URL    
    };

    //     
    const handleEventClick = (arg) => {
        console.log('Clicked event:', arg); //     .
        console.log('Clicked event arg.event.id:', arg.event.id);
        navigate(`/getDiary/${arg.event.id}/${hostId}`); //   ID URL    
    };

    //   
    const eventContent = (eventInfo) => {
        return (
            <div className="event-content">
                <strong  style={{ cursor: 'pointer' }}>{eventInfo.event.title}</strong>
            </div>
        );
    };

    return (
        <div className="calendar-box">
            <div className="calendar-back"> 
                <div className="calendar-container">
                    <FullCalendar
                        plugins={[dayGridPlugin, interactionPlugin]} //    
                        initialView="dayGridMonth" //    
                        events={events.map(event => ({
                            title: event.dTitle, //  
                            id: event.dnum, //  ID
                            start: event.start, //   
                            end: event.end //   
                        }))}
                        dateClick={handleDateClick} //    
                        eventClick={handleEventClick} //    
                        eventContent={eventContent} //   
                        locale={koLocale} //   
                    />
                </div>

            </div>
        </div>
    );
}

export default MyCalendar;
