import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import './Login.css';
import MainPanel from '../mainPanel/MainPanel';
import CharacterImg from '../../img/characterImg/loginImg2.png';
import Stars from './Stars';  //    

const Login = ({ onLoginSuccess }) => {
  const [userid, setUserid] = useState('');
  const [pass, setPass] = useState('');
  const [message, setMessage] = useState('');
  const [showMessage, setShowMessage] = useState(false);  //   
  const navigate = useNavigate();

  const handleLogin = async () => {
    console.log(' :', userid, pass);
    try {
      const result = await axios.post('/member/login', {
        memId: userid,
        pass: pass,
      });
      if (result.data && result.data.length >= 2 && result.data[0]) {
        localStorage.clear();
        localStorage.setItem('id', result.data[1]); // memId 
        localStorage.setItem('token', result.data[0]); //  
        sessionStorage.setItem('login', true); //   

        setMessage(' !');
        onLoginSuccess(); //    
        navigate(`/home/${result.data[1]}`); //   
      } else {
        setMessage('    ');
        setShowMessage(true);  //  
      }
    } catch (error) {
      console.error(' :', error);
      setMessage(' :   .');
      setShowMessage(true);  //  
    }
  };

  const handleKeyPress = (event) => {
    if (event.key === 'Enter') {
      handleLogin();
    }
  };

  // 5     
  useEffect(() => {
    if (showMessage) {
      const timer = setTimeout(() => {
        setShowMessage(false);  //  
      }, 5000);  // 5 

      return () => clearTimeout(timer);  //  
    }
  }, [showMessage]);

  return (
    <div className="loginBox">
      {localStorage.getItem('token') ? (
        <div>
          <MainPanel />
        </div>
      ) : (
        <div className="login-form">
          <h2 className="loginAccount">Login Account</h2>
          {showMessage && (
        <p className={`loginErrorMessage ${showMessage ? '' : 'hidden'}`}>
          {message}
        </p>
      )}
          <table>
            <tr>
              <input
                type="text"
                placeholder="ID"
                className="input-field"
                value={userid}
                onChange={(e) => setUserid(e.target.value)}
                onKeyPress={handleKeyPress} // Add key press handler here
              />
            </tr>
            <tr>
              <input
                type="password"
                placeholder="Password"
                className="input-field"
                value={pass}
                onChange={(e) => setPass(e.target.value)}
                onKeyPress={handleKeyPress} // Add key press handler here
              />
            </tr>
            <td rowSpan="2">
              <button className="login-button" onClick={handleLogin}>Login</button>
            </td>
          </table>
          <table className="bu">
            <tr>
              <td><a href="/SignUpPage" className="signUp"></a></td>
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</td>
              <td><a href="/FindIdPassword" className="find">/ </a></td>
            </tr>
          </table>
          <p className="description">
               ,   .
            <br />
               
            <br />
             ,      !
          </p>
          <div className="light"></div>
          <div><img className="loginImg-box" src={CharacterImg}/></div>
        </div>
      )}
    </div>
  );
}

export default Login;
