import React from 'react';
import './Stars.css'; //   CSS 

//    
const generateStars = (count) => {
  const stars = [];
  for (let i = 0; i < count; i++) {
    const starStyle = {
      top: `${Math.random() * 100}%`,
      left: `${Math.random() * 100}%`,
      animationDuration: `${Math.random() * 2 + 1.5}s`, // 1.5 3.5  
    };
    stars.push(<div className="star" key={i} style={starStyle}></div>);
  }
  return stars;
};

//   
const Stars = () => {
  return <div className="stars-container">{generateStars(100)}</div>; //  100 
};

export default Stars;
