import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';
import './GetDiary.css';

function GetDiary() {
    const { dnum, hostId } = useParams(); // URL dNum 
    const navigate = useNavigate();
    const [diary, setDiary] = useState({});
    console.log('dnum :', dnum);
    
    useEffect(() => {
        if (dnum && hostId) {
            axios.get(`/api/events/getDiary/${dnum}/${hostId}`) // dNum URL    
                .then(response => {
                    console.log('', diary);
                    console.log('', response);
                    if (response.data) {
                        console.log('response:', response);
                        setDiary(response.data);
                    } else {
                        setDiary(null);
                    }
                })
                .catch(error => {
                    console.error('Error fetching diary:', error);
                    setDiary(null); //    null 
                });
        }
    }, [dnum]);

    const handleDelete = () => {
        if (window.confirm('  ?')) {
            axios.delete(`/api/events/delete/${dnum}/${hostId}`)
                .then(() => {
                    alert(' .');
                    navigate(`/diary/${hostId}`); //    
                })
                .catch(error => {
                    console.error('Error deleting diary:', error);
                });
        }
    };

    if (diary === null) {
        return <div>    .</div>;
    }

    // `diary.start`    substring 
    const formattedDate = diary.start ? diary.start.substring(0, 10) : ' ';

    return (
        <div className="diary-box">
            <h2 className='title'>{diary.dTitle}</h2>
            <p className='content'>{diary.dContent}</p>
            <p className='setDay'>{`: ${formattedDate}`}</p>
            {/* <p>{`: ${diary.end}`}</p> */}
            <button onClick={handleDelete} className='deleteButton'></button>
        </div>
    );
}

export default GetDiary;
