import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './WriteMessage.css';

const WriteMessage = ({ recipient }) => {
  const [receiver, setReceiver] = useState(recipient || '');  //   
  const [mContent, setmContent] = useState('');  //   
  const senderId = localStorage.getItem("id");  //  ID

  //    (  )
  useEffect(() => {
    if (recipient) {
      setReceiver(recipient);  //       
    }
  }, [recipient]);

  const sendMessage = () => {
    //     
    if (receiver === senderId) {
      alert("     .");
      return;
    }

    axios.post('/api/messages/send', {
      memId: senderId,  //  ID
      friendId: receiver,  //  ID
      mcontent: mContent   //  
    })
    .then(() => {
      alert(`${receiver}   `);
      setmContent('');  //   
      setReceiver('');  //   
    })
    .catch(error => console.error("Error sending message:", error));
  };

  return (
    <div className='writeMessageContainer'>
      <h5 className="WM-btn"> </h5>
      <input
        className="WriteMessage"
        type="text"
        placeholder=" "
        value={receiver}
        onChange={e => setReceiver(e.target.value)}  //   
      /><br/>
      <textarea
        type="text"
        placeholder=" "
        value={mContent}
        onChange={e => setmContent(e.target.value)}  //    
      /><br/><br/>
      <button className="WM-Sbtn"onClick={sendMessage}></button>  {/*    */}
    </div>
  );
}

export default WriteMessage;
