// src/components/FriendsList.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './FriendsList.css';

function FriendsList({ hostId, setHostId }) {
  const [friends, setFriends] = useState([]); //    
  const navigate = useNavigate();

  //    
  const fetchFriends = () => {
    axios.get('/friends/total', {
      params: { memId: localStorage.getItem('id') }, //    ID
    })
      .then(response => {
        console.log('  :', response.data); //    
        setFriends(response.data); //   
      })
      .catch(error => console.error('     ', error)); //  
  };

  useEffect(() => {
    fetchFriends(); //      
  }, []);

  //  
  const deleteFriend = (fNum) => {
    if (window.confirm('  ?')) {  //  
      console.log(`   fNum: ${fNum}`);

      axios.post(`/friends/delete`, null, {
        params: { fNum: fNum },
      })
        .then(() => {
          console.log(` : ${fNum}`);
          fetchFriends(); //       
        })
        .catch(error => {
          console.error('    ', error);
        });
    }
  };

  //   
  const goFriendHome = (friendId) => {
    setHostId(friendId);
    console.log(' : ', friendId);
    navigate(`/home/${friendId}`); //   
  };

  return (
    <div className="friends-section-list">
      <h3 className="list-friends"> </h3>
      {friends.length === 0 ? ( //      
        <p>  .<br/>   !</p>
      ) : (
        <ul>
          {friends.map(friend => (
            <li key={friend.fNum}>
              <span className="f-list" onClick={() => goFriendHome(friend.friendId)}>{friend.friendId}</span>
              <button onClick={() => deleteFriend(friend.fNum)}></button> 
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}

export default FriendsList;
