import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import Slider from "react-slick";
import './BoardDetailCss.css';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

function BoardDetail({ hostId }) {
  const { bNum } = useParams();  // URL  bNum .
  const [detail, setDetail] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    axios.get('/board/detail', { params: { bNum } })  // bNum  
      .then((response) => {
        console.log("Fetched detail:", response.data);
        setDetail(response.data);
      })
      .catch((error) => {
        console.error("Error fetching detail:", error);
        alert("     .");
      });
  }, [bNum]);  // bNum   useEffect 

  const handleDelete = () => {
    if (window.confirm(" ?")) {
      axios.delete(`/board/delete/${bNum}`)
        .then(() => {
          alert(" .");
          navigate(`/bulletin-board/${ hostId }`);  //    
        })
        .catch((error) => {
          console.error("Error deleting post:", error);
          alert("    .");
        });
    }
  };

  const handleEdit = () => {
    navigate(`/boardEdit/${bNum}`);  //   
  };

  const BoardList = () => {
    navigate(`/bulletin-board/${ hostId }`);  //   
  };

  if (!detail) {
    return <div>Loading...</div>;  //      
  }
  

  //   (   infinite  )
  const sliderSettings = {
    dots: true,
    infinite: detail.imgPath.length > 1,  //  1  infinite  
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: false,
  };

  return (
    <div className="board-in">
      <div className="board-title-inbox">
        <button onClick={BoardList}
        className="board-listback" ></button>
        {/*     hostId loggedInUserId    */}
        {hostId === localStorage.getItem("id") && (
          <div>
            <button onClick={handleEdit} className="board-edit-btn"></button>
            <button onClick={handleDelete} className="board-edit-btn"></button>
          </div>
        )}
      </div>

      {/*   */}
      <div className="board-content-box">
      <Slider {...sliderSettings}>
        {detail.imgPath.map((img, index) => (
          <div key={index}>
            <img
              className="board-img-in"
              src={img }
              alt={`Image ${index + 1}`}
            />
          </div>
        ))}
      </Slider>
      <div className="board-text-box">
        <h2 className="board-title-in">{detail.btitle}</h2>
        <p className="board-content-in">{detail.bcontent}</p>
        <p>{detail.bNum}</p>
      </div>
      </div>
    </div>
  );
}

export default BoardDetail;
