import React, { useState, useEffect } from 'react';
import axios from 'axios'; // axios 
import './SignUpPage.css'; // SignUpPage  
import { useNavigate } from 'react-router-dom';

function SignUpPage() {
  const [formData, setFormData] = useState({
    userId: '',
    password: '',
    nickname: '',
    name: '',
    birthDate: '',
    address: '',
    gender: '',
    phoneNumber: '',
    email: '',
  });
  const [selectedFile, setSelectedFile] = useState(null); //   
  const [isIdAvailable, setIsIdAvailable] = useState(null); //    
  const [isSignUpDisabled, setIsSignUpDisabled] = useState(true); //    

  const navigate = useNavigate();

  //   5     
  useEffect(() => {
    const checkUserId = async () => {
      if (formData.userId.length >= 5) {
        try {
          const response = await axios.get('/member/checkId', {
            params: { userId: formData.userId },
          });
          if (response.data) {
            setIsIdAvailable(false); //  
            setIsSignUpDisabled(true); //  
          } else {
            setIsIdAvailable(true); //   
            setIsSignUpDisabled(false); //  
          }
        } catch (error) {
          console.error('    :', error);
        }
      } else {
        setIsIdAvailable(null); //   5    
        setIsSignUpDisabled(true); //  
      }
    };

    checkUserId();
  }, [formData.userId]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleFileChange = (e) => {
    setSelectedFile(e.target.files[0]); //     
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const data = new FormData();
    // FormData   
    data.append('member', new Blob([JSON.stringify({
      memId: formData.userId,
      pass: formData.password,
      nickname: formData.nickname,
      name: formData.name,
      birthday: formData.birthDate,
      address: formData.address,
      gender: formData.gender,
      phone: formData.phoneNumber,
      email: formData.email,
    })], { type: 'application/json' }));

    //   FormData 
    if (selectedFile) {
      data.append('image', selectedFile);
    }

    try {
      await axios.post('/member/signup', data, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      alert(' .');
      navigate("/");
    } catch (error) {
      alert('   .');
    }
  };

  return (
    <div className="signup-container">
      <h1>Sign Up</h1>
      <form onSubmit={handleSubmit}>
        <p>  </p>
        <input
          type="text"
          name="userId"
          placeholder=""
          value={formData.userId}
          onChange={handleChange}
          required
        />
        {isIdAvailable === true && (
          <p style={{ color: 'green' }}>   .</p>
        )}
        {isIdAvailable === false && (
          <p style={{ color: 'red' }}> .</p>
        )}

        <input
          type="password"
          name="password"
          placeholder=""
          value={formData.password}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="nickname"
          placeholder=""
          value={formData.nickname}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="name"
          placeholder=""
          value={formData.name}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="birthDate"
          placeholder=" 8"
          value={formData.birthDate}
          onChange={handleChange}
          required
        />
        <input
          type="text"
          name="address"
          placeholder=""
          value={formData.address}
          onChange={handleChange}
          required
        />
        <div className="gender-selection">
          <label>:</label>
          <label className="gender-label">
            <input
              type="radio"
              name="gender"
              value=""
              checked={formData.gender === ''}
              onChange={handleChange}
              required
            />
            
          </label>
          <label className="gender-label">
            <input
              type="radio"
              name="gender"
              value=""
              checked={formData.gender === ''}
              onChange={handleChange}
            />
            
          </label>
        </div>
        <input
          type="text"
          name="phoneNumber"
          placeholder=" "
          value={formData.phoneNumber}
          onChange={handleChange}
          required
        />
        <p>*  </p>
        <input
          type="email"
          name="email"
          placeholder="[]  (   )"
          value={formData.email}
          onChange={handleChange}
        />

        <button className="signup-submit" type="submit" disabled={isSignUpDisabled}></button>
      </form>

      {/*     */}
      <button className="go-home-button" onClick={() => {navigate('/')}}>
         
      </button>
    </div>
  );
}

export default SignUpPage;
